import logo from './logo.svg';
import { useState } from "react";
import './App.css';
import { upperCase } from 'lodash';
import samsung from './samsung.jpg';
import apple from './apple.jpg';
import mi from './mi-11.jpeg';
import oppo from './oppo.jpg';


function App() {
  return <div class="container">
    <Vote brandName="Samsung" model="S21 Ultra" url={samsung} />
    <Vote brandName="Apple" model="S11" url={apple} />
    <Vote brandName="Mi" model="Mi 11 X Pro" url={mi} />
    <Vote brandName="Oppo" model="Oppo 11 Ultra" url={oppo} />
  </div>

}

function Vote(props) {
  const [like, setLike] = useState(0);
  const [dislike, setDisLike] = useState(0);
  // const likeBackground = background-color:palegreen
  // const likeBackground = background-color:crimson

  return (
    <div className="App" style={{
      background: like >= dislike ? 'green' : 'crimson',
      textTransform: upperCase
    }}
    >

      <h1>{props.brandName}</h1>
      <h4>{props.model}</h4>
      <img src={props.url}></img>
      <button onClick={() => setLike(like + 1)}>
        üëç {like}</button>
      <button onClick={() => setDisLike(dislike + 1)}>

        üëé {dislike}</button>

    </div>
  );
}

export default App;
